<template>
    <!--  H5 -->
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row :gutter="5">
                <el-col :span="15">
                    <div style="display: flex">
                        <div class="dropdown-menu" style="height:30px; width:120px">
                            <el-dropdown  @command="dropdownHandleCommand" trigger="click" style="margin-top: 10px">
                                <span><i class="el-icon-arrow-down el-icon--right"></i></span>
                                <template #dropdown>
                                    <el-dropdown-menu>
                                        <el-dropdown-item :command="key" v-for="(val,key) in queryConditionMap">{{val}}</el-dropdown-item>
                                    </el-dropdown-menu>
                                </template>
                            </el-dropdown>
                        </div>
                        <el-input :placeholder="inputTip" v-model="queryInfo.query" clearable style="width: 300px"></el-input>
                        <el-button slot="append" icon="el-icon-search" @click="getStudentListByCondition"></el-button>
                        <div style="width: 200px;height: 40px;">
                            <el-switch style="display: block;margin-top: 10px;margin-left: 20px;" v-model="switchValue" inactive-color="#13ce66"
                                       @click="switchHandel" :disabled="switchDisabled" active-text="" inactive-text="">
                            </el-switch>
                        </div>
                    </div>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addStudentInfoShowDialog"></el-button>
                </el-col>
            </el-row>
            <el-table :data="studentList" border stripe style="margin-top: 20px">
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="number" align="center" width="150px"></el-table-column>
                <el-table-column label="" prop="name"  align="center" width="150px"></el-table-column>
                <el-table-column label="" prop="classNum"  align="center" width="70px"></el-table-column>
                <el-table-column label="" prop="id"  align="center" width="250px"></el-table-column>
                <el-table-column label="" prop="phone"  align="center" width="200px"></el-table-column>
                <el-table-column label="" prop="sex"  align="center" width="70px"></el-table-column>
                <el-table-column label="" prop="age"  align="center" width="70px"></el-table-column>
                <el-table-column label="" prop="ifViolated" width="70px">
                    <template #default="scope">
                        <el-switch v-model="scope.row.ifViolated" @change="studentStateChangedHandel(scope.row)"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label=""  align="center" >
                    <template #default="scope">
                        <el-button size="mini" type="primary" @click="editStudentInfoShowDialog(scope.$index, scope.row)"></el-button>
                        <el-button size="mini" type="danger" @click="delStudentInfoShowDialog(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination v-if="ifShowPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum"
                :page-sizes="[15, 20, 30, 40]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>
        <el-dialog title="" v-model="centerDialogVisible" width="30%" center :close-on-click-modal="false">
            <el-form :model="editStudentInfo" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.number" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.name" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.classNum" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.id" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.phone"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.sex"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editStudentInfo.age" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                  <el-button @click="centerDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="editStudentInfoHandel"> </el-button>
                </span>
            </template>
        </el-dialog>
        <el-dialog title="" v-model="addStudentDialogVisible" width="30%">
            <el-form :model="addStudent">
                <el-form-item label="" label-width="120px">
                    <el-input v-model="addStudent.name" ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="addStudent.classNum" ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="addStudent.id" ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="addStudent.phone" ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="addStudent.sex" ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="addStudent.age"></el-input>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                  <el-button @click="addStudentDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="addStudentInfoHandel"> </el-button>
                </span>
            </template>
        </el-dialog>
        <el-dialog title="" v-model="delStudentDialogVisible" width="30%" >
            <span></span>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="delStudentDialogVisible = false;delStudent={}"> </el-button>
                    <el-button type="danger" @click="delStudentInfoHandel"> </el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>

<script>
//js Code
import axios from "axios";

export default{
    //
    name:'StudentInfoPage',
    data(){
        return{
            //
            queryInfo: {
                query: '',
                pageNum: 1,
                pageSize: 20,
                condition:'',
                // 
                ifFuzzySearch:false,
            },
            // 
            total: 1306,
            studentList:[],
            inputTip:"",
            queryConditionShow:'',
            ifShowPagination:true,
            queryConditionList:['','','','','','','','',''],
            queryConditionMap:{
                "all":"",
                "number"     :"",
                "name"       :"",
                "class_num"   :"",
                // "classNum"   :"",
                "id"         :"",
                "phone"      :"",
                "sex"        :"",
                "age"        :"",
                "if_violated" :"",
                // "ifViolated" :"",
            },
            // false   true
            switchValue:false,
            // 
            switchDisabled:false,
            centerDialogVisible: false,
            editStudentInfo:{},
            addStudentDialogVisible:false,
            delStudentDialogVisible:false,
            delStudent:{},
            addStudent: {
                name: '',
                id: '',
                phone: '',
                sex: '',
                age: '',
                classNum: '',
            },
        }
    },
    created() {
        this.getStudentList()
    },
    methods:{
        async getStudentList() {
            const {data: res} = await this.$http.get('stu-info/get', {params: this.queryInfo})
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.studentList = res.data.infoList
            this.total = res.data.numOfTotalRecord
            this.ifShowPagination = true;
        },
        // 
        async getStudentListByCondition() {
            this.queryInfo.ifFuzzySearch = this.switchValue
            if(this.queryInfo.query === '' ) return this.$message.error("")
            const {data: res} = await this.$http.get('stu-info/get-test', {params: this.queryInfo})
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.studentList = res.data.infoList;
            this.total = res.data.numOfTotalRecord;
            if(this.queryInfo.condition === 'class_num'){
                this.ifShowPagination = false;
            }else{
                this.ifShowPagination = true;
            }
        },
        dropdownHandleCommand(command){
            let commandKey = this.queryConditionMap[command]
            if(commandKey === ''){
                // 
                this.switchDisabled = true
                // 
                this.ifShowPagination = true;
                this.queryInfo.pageNum = 1
                this.queryInfo.pageSize = 20
                this.getStudentList()
                return
            }
            // 
            if(commandKey === '' || commandKey === ''|| commandKey === ''|| commandKey === ''){
                this.switchValue = false
                this.switchDisabled = true
            }else{
                this.switchDisabled = false
            }
            this.queryInfo.condition = command
            this.queryInfo.ifFuzzySearch = this.switchValue
            this.inputTip = ":"+ commandKey
        },
        handleSizeChange(newSize) {
            this.queryInfo.pageSize = newSize
            this.getStudentList()
        },
        handleCurrentChange(newSize) {
            this.queryInfo.pageNum = newSize
            this.getStudentList()
        },
        addStudentInfoShowDialog() {
            this.addStudentDialogVisible = true
            // this.getCateList()
            //this.$router.push('/goods/add')
        },
        editStudentInfoShowDialog(index,row){
            this.centerDialogVisible = true
            this.editStudentInfo = row
        },
        async editStudentInfoHandel() {
            if(this.editStudentInfo.phone.length !== 11){
                return this.$message.error("")
            }
            if(this.editStudentInfo.sex !== '' && this.editStudentInfo.sex !== ''){
                return this.$message.error("")
            }
            const {data: res} = await this.$http.post('stu-info/edit',this.editStudentInfo)
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('')
            this.centerDialogVisible = false
            await this.getStudentList()
        },
        //  switch 
        async studentStateChangedHandel(stuInfo) {
            const {data: res} = await this.$http.post('stu-info/change',{"ifViolated":stuInfo.ifViolated,"id":stuInfo.id})
            if (res.meta.status !== 200) {
                stuInfo.ifViolated = !stuInfo.ifViolated
                return this.$message.error('')
            }
            this.$message.success('')
        },
        //
        async addStudentInfoHandel(){
            this.addStudent.age = Number(this.addStudent.age)
            if(this.addStudent.name === '' )return this.$message.error("")
            if(this.addStudent.classNum <= 0 || this.addStudent.classNum >20 )return this.$message.error("")
            if(this.addStudent.id.length !== 18)return this.$message.error("")
            if(this.addStudent.phone.length !== 11) return this.$message.error("")
            if(this.addStudent.sex !== '' && this.addStudent.sex !== '')return this.$message.error("")
            if(this.addStudent.age <= 0 || this.addStudent.age >30 )return this.$message.error("")

            const {data:res} = await axios.post('stu-info/add', {
                "name":this.addStudent.name,
                "classNum":this.addStudent.classNum,
                "id":this.addStudent.id,
                "phone":this.addStudent.phone,
                "sex":this.addStudent.sex,
                "age":this.addStudent.age,
            })
            //
            this.addStudent.name = this.addStudent.number =  this.addStudent.id = this.addStudent.classNum = ''
            this.addStudent.phone = this.addStudent.age = this.addStudent.sex = ''
            //
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }else{
                this.$message.success(res.meta.msg)
            }
            this.addStudentDialogVisible = false
        },
        // 
        delStudentInfoShowDialog(index,row){
            this.delStudentDialogVisible = true
            this.delStudent = row
        },
        async delStudentInfoHandel(){
            const {data:res} = await axios.delete("stu-info/delete",{data:this.delStudent})
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.delStudentDialogVisible = false
            this.$message.success("")
            await this.getStudentList()
        },
        switchHandel(){
            // this.$message.success(this.switchValue)
        },
    }
}
</script>

<style scoped lang="less">
//css
    .el-card{
        margin-top: 20px;
    }
</style>
