<template>
	<div class="login_container">
		<div class="login_box">
			<!-- -->
			<div class="avatar_box">
				<img src="../assets/ai.jpg" alt="" />
			</div>

			<el-tabs v-model="activeName"  class ="loginTags">
                <el-tab-pane label="" name="first">
                    <!--  -->
                    <el-form ref="loginByPasswordRef" :model="loginForm" :rules="loginByPasswordRules" label-width="70px" class="login_form">
                        <!--  -->
                        <el-form-item label="" prop="username">
                            <el-input v-model="loginForm.username" class="login_username" prefix-icon="el-icon-s-custom" type="username" style="width: 325px;"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-form-item class="login_username" style="display: flex">
                                <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password" style="width: 325px;"></el-input>
                            </el-form-item>
                        </el-form-item>
                        <!-- -->
                        <el-form-item class="btns">
                            <el-button type="primary" @click="loginByPassword"></el-button>
                            <el-button type="info" @click="resetLoginFrom"></el-button>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
			    <el-tab-pane label="" name="second">
					<!--  -->
					<el-form ref="loginByPhoneRef" :model="loginForm" :rules="loginByPhoneRules" label-width="70px" class="login_form">
						<!--  -->
						<el-form-item label="" prop="phone">
							<el-input v-model="loginForm.phone" class="login_username" prefix-icon="el-icon-s-custom" type="username" style="width: 325px;"></el-input>
						</el-form-item>
						<el-form-item label="" prop="code">
							<el-form-item class="login_username">
								<el-input v-model="loginForm.code" prefix-icon="el-icon-lock" type="password" style="width: 200px;"></el-input>
								<el-button v-model="smsCode" type="primary" plain @click="sendSms" v-if="!smsCode.is_disabled" style="font-size: 8px;">{{smsCode.content}}</el-button>
								<el-button v-model="smsCode" type="primary" plain v-if="smsCode.is_disabled" style="font-size: 8px;">{{smsCode.content}}</el-button>
							</el-form-item>
						</el-form-item>
						<!-- -->
						<el-form-item class="btns">
							<el-button type="primary" @click="loginBySmsCode"></el-button>
							<el-button type="info" @click="resetLoginFrom"></el-button>
						</el-form-item>
					</el-form>
				</el-tab-pane>

			</el-tabs>
			

		</div>
	</div>
</template>

<script>
	import axios from "axios";

    export default {
		data() {
			return {
				loginForm: {
					phone: "18366628806",
					code: "",
					username:"EdmundShelby",//1575079696
					password:"123456",
				},
				//
				loginByPhoneRules: {
					//
					phone: [
						{ required: true,message: "",trigger: "blur"},
						{ min: 5,max: 19,message: "5-19",trigger: "blur"}
					],
					code: [
						{ required: true,message: "",trigger: "blur"},
						{ min: 6,max: 16,message: "",trigger: "blur"}
					],
				},
				loginByPasswordRules:{
					username: [
						{ required: true,message: "",trigger: "blur"},
						{ min: 5,max: 19,message: "",trigger: "blur"}
					],
					password: [
						{ required: true,message: "",trigger: "blur"},
						{ min: 6,max: 16,message: "5-19",trigger: "blur"}
					]
				},
				smsCode: {
					is_disabled: false, //false:true:
					content: '', // 
					timer: null,
					count: '',
					model: {},
				},
				activeName:'first',
			};
		},
		methods: {
			//
			resetLoginFrom() {
				this.$refs.loginByPasswordRef.resetFields(); //.loginFormRef.re
			},
			// way=trueway=false
			async login(validWay,user,valid){
				//datares
                if(validWay){
                //    
                    return this.$message.success(" : ")
                }else{
                    const {data: res} = await axios.post("/stu-user/login",{"account":user,"password": valid,"captcha": "no captcha"})
                    if (res.meta.status !== 200) {
                        return this.$message.error(res.meta.msg);
                    }
				    this.$message.success(", " + res.data.account + " !");
                    //tokensessionStorage
                    window.sessionStorage.setItem("token", res.data.token);
                    //
                    await this.$router.push('/home');
                }
			},
			// 
			loginBySmsCode() {
				this.$refs.loginByPhoneRef.validate(async valid => {
					if (!valid) return;
					await this.login(true, this.loginForm.phone, this.loginForm.code);
				});
			},
			// 
			loginByPassword(){
				this.$refs.loginByPasswordRef.validate(async valid => {
					if (!valid) return;
					await this.login(false, this.loginForm.username, this.loginForm.password);
				});
			},
			// 
			sendSms() {
				let vm = this.smsCode
				// 
				if (!this.loginForm.phone || this.loginForm.phone === '') {
					return this.$message.error("");
				}
				if (!(/^1[34578]\d{9}$/.test(this.loginForm.phone))) {
					return this.$message.error("");
				}
				// 
                /*
                    ,
				    this.$http.get("sendcode?phone=" + this.loginForm.phone); //GET
				*/
				// 

				const TIME_COUNT = 60
				vm.count = TIME_COUNT
				vm.timer = window.setInterval(() => {
					if (vm.count > 0 && vm.count <= TIME_COUNT) {
						// 
						vm.is_disabled = true
						// 
						vm.count--
						// 
						vm.content = vm.count + 's'
					} else {
						// 
						vm.is_disabled = false
						// 
						vm.content = ''
						// !!!
						clearInterval(vm.timer)
						vm.timer = null
					}
				}, 1000)
                return this.$message.success(" : ")
			},
		}
	};
</script>

<style lang="less" scoped>
	.login_container {
		background-image: url(../assets/as.jpg);
		background-size: 100% 100%;
		height: 100%;
	}

	.login_box {
		width: 530px;
		height: 370px;
		background-color: #FFFFFF;
		border-radius: 15px;
		left: 50%;
		top: 50%;
		position: absolute;
		transform: translate(-50%, -50%);

		.avatar_box {
			padding: 10px;
			height: 130px;
			width: 130px;
			box-shadow: 0 0 10px #ddd;
			border: 1px, solid #eee;
			border-radius: 50%;
			position: absolute;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: #fff;

			img {
				width: 100%;
				height: 100%;
				border-radius: 50%;
				background-color: #eee;
			}
		}
	}
	.loginTags{
		margin-top: 80px;
		margin-left: 40px;
		width: 450px;
		text-align: center;
	}
	.login_form {
		//position: absolute;
		bottom: 0;
		width: 100%;
		padding: 0 20px;
		margin-top: 20px;
		box-sizing: border-box;
		//
	}

	.login_username {
		 display: flex;
	//justify-content: flex-end; */
	}

	.btns {
		display: flex;
		justify-content: flex-end;
	}
</style>
