<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-button type="primary" @click="addDialogVisible = true"></el-button>
            <el-table :data="userlist" border stripe>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="id" label="ID" align="center" header-align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center" header-align="center"></el-table-column>
                <el-table-column prop="phone" label="" align="center" header-align="center"></el-table-column>
                <el-table-column label="" align="center" header-align="center">
                    <template #default="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" circle @click="showEditDialog(scope.row)"></el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="removeUserById(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
                           :page-sizes="[2, 5, 10, 15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper"
                           :total="total"></el-pagination>
        </el-card>
        <el-dialog title="" v-model="addDialogVisible" width="35%">
            <el-form :model="addUserForm" label-width="100px">
                <el-form-item label="" prop="username">
                    <el-input v-model="addUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="addUserForm.password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mobile">
                    <el-input v-model="addUserForm.phone"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
				<el-button @click="addDialogVisible = false"> </el-button>
				<el-button type="primary" @click="addUser"> </el-button>
			</span>
        </el-dialog>

        <el-dialog title="" v-model="editDialogVisible" width="30%" center :close-on-click-modal="false">
            <el-form :model="editUserForm" label-width="80px">
                <el-form-item label="id">
                    <el-input v-model="editUserForm.id" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.phone"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.password"></el-input>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                  <el-button @click="editDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="editUser"> </el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>

<script>

export default {
    name:"UserManagement",
    data() {
        return {
            // 
            queryInfo: {
                query: '',
                // 
                pagenum: 1,
                // 
                pagesize: 10
            },
            userlist: [],
            total: 0,
            // 
            addDialogVisible: false,
            // 
            addUserForm: {
                name: '',
                password: '',
                phone: ''
            },
            // 
            editDialogVisible: false,
            editUserForm: {},
        }
    },
    created() {
        this.getUserList()
    },
    methods: {
        async getUserList() {
            const {data: res} = await this.$http.get('stu-user/user-list', {params: this.queryInfo})
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            console.log(res)
            this.userlist = res.data.infoList
            this.total = res.data.numOfTotalRecord
        },
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getUserList()
        },
        handleCurrentChange(newSize) {
            this.queryInfo.pagenum = newSize
            this.getUserList()
        },
        async addUser() {
            console.log(this.addUserForm)
            const {data: res} = await this.$http.post('stu-user/save', this.addUserForm)
            if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg)
            }
            this.$message.success(''+'Id:'+res.data.id)
            // 
            this.addDialogVisible = false
            await this.getUserList()
        },
        async showEditDialog(row) {
            // this.getDatePicker = res.data.birthday
            // this.editUserForm = res.data
            this.editUserForm = row
            console.log(this.editUserForm)
            this.editDialogVisible = true
        },
        async editUser() {
            console.log(this.editUserForm)
            const {data: res} = await this.$http.post('/stu-user/edit',this.editUserForm)
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            // 
            this.editDialogVisible = false
            this.$message.success('')
            await this.getUserList()
        },
        async removeUserById(id) {
            const confirmResult = await this.$confirm(
                ', ?',
                '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }
            ).catch(err => err)
            if (confirmResult !== 'confirm') {
                return this.$message.info('')
            }
            const {data: res} = await this.$http.delete('/stu-user/del', {data:id})
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('')
            await this.getUserList()
        },

    }
}
</script>

<style lang="less" scoped>

.user-detail-data-label{
    padding-left: 30px;
    font-size: 17px;
}

</style>
