<template>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <el-card style="width: 100%;height: auto">
        <el-button type="primary" @click="addTeacherDialogVisible = true"></el-button>
        <el-table :data="teacherList" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" align="center" prop="name"></el-table-column>
            <el-table-column label="ID" align="center" prop="id"></el-table-column>
            <el-table-column label="" align="center"  prop="idNum"></el-table-column>
            <el-table-column label="" align="center" prop="phone"></el-table-column>
            <el-table-column label="" align="center" prop="email"></el-table-column>
            <el-table-column label="" align="center" prop="ed" width="100px"></el-table-column>
            <el-table-column label="" align="center" prop="sex" width="100px"></el-table-column>
            <el-table-column label="" align="center" prop="age" width="100px"></el-table-column>
            <el-table-column label=""  align="center" >
                <template #default="scope">
                    <el-button size="mini" type="primary" @click="editTeacherInfoShowDialog(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" type="danger" @click="delTeacherInfoShowDialog(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum"
                       :page-sizes="[15, 20, 30, 40]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
    </el-card>
    <el-dialog title="" v-model="addTeacherDialogVisible" width="30%">
        <el-form :model="addTeacher">
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.name" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.idNum" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.phone" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.email" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.ed" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.sex" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addTeacher.age"></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
                <span class="dialog-footer">
                  <el-button @click="addTeacherDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="addTeacherInfoHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
    <el-dialog title="" v-model="centerDialogVisible" width="25%" center :close-on-click-modal="false">
        <el-form :model="editTeacherInfo" label-width="80px">
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.name" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="ID">
                <el-input v-model="editTeacherInfo.id" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.idNum" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.phone" ></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.email"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.ed"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.age"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="editTeacherInfo.sex"></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
                <span class="dialog-footer">
                  <el-button @click="centerDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="editTeacherInfoHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
    <el-dialog title="" v-model="delTeacherDialogVisible" width="30%" >
        <span></span>
        <template #footer>
                <span class="dialog-footer">
                    <el-button @click="delTeacherDialogVisible = false;delTeacher={}"> </el-button>
                    <el-button type="danger" @click="delTeacherInfoHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
</template>

<script>
import axios from "axios";

export default {
    name: "TeacherInfos",
    data(){
        return{
            //
            queryInfo:{
                pageNum:1,
                pageSize:20
            },
            teacherList:[],
            total:0,
            centerDialogVisible:false,
            editTeacherInfo:{},
            delTeacherDialogVisible:false,
            delTeacher:{},
            addTeacherDialogVisible:false,
            addTeacher:{
                name:'',
                phone:'',
                idNum:'',
                email:'',
                age:'',
                sex:'',
                ed:'',
            }
        }
    },
    created() {
        this.getTeacherInfoList()
    },
    methods:{
        async getTeacherInfoList(){
            const {data:res} = await axios.get("stu-teacher-info/get",{params:this.queryInfo})
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.teacherList = res.data.infoList
            this.total = res.data.numOfTotalRecord
        },
        handleSizeChange(newSize) {
            this.queryInfo.pageSize = newSize
            this.getTeacherInfoList()
        },
        handleCurrentChange(newSize) {
            this.queryInfo.pageNum = newSize
            this.getTeacherInfoList()
        },
        editTeacherInfoShowDialog(index,row){
            this.centerDialogVisible = true
            this.editTeacherInfo = row
        },
        // 
        delTeacherInfoShowDialog(index,row){
            this.delTeacherDialogVisible = true
            this.delTeacher = row
        },
        async editTeacherInfoHandel(){
            if(this.editTeacherInfo.phone.length !== 11)return this.$message.error("")
            if(this.editTeacherInfo.sex !== '' && this.editTeacherInfo.sex !== '')return this.$message.error("")
            if(this.editTeacherInfo.ed !=='' && this.editTeacherInfo.ed !=='' && this.editTeacherInfo.ed !==''){
                return this.$message.error("")
            }
            if(this.editTeacherInfo.age < 0 || this.editTeacherInfo.age > 120)return this.$message.error("")
            const {data: res} = await this.$http.post('stu-teacher-info/edit',this.editTeacherInfo)
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('')
            this.centerDialogVisible = false
            await this.getTeacherInfoList()
        },
        async delTeacherInfoHandel(){

            const {data:res} = await axios.delete("stu-teacher-info/del",{data:this.delTeacher})
            if(res.meta.status !== 200){
                return this.$message.error("")
            }
            this.delTeacherDialogVisible = false
            this.$message.success("")
            await this.getTeacherInfoList()
        },
        //
        async addTeacherInfoHandel(){
            if(this.addTeacher.name === '') return this.$message.error("")
            if(this.addTeacher.idNum.length !== 18)return this.$message.error("")
            if(this.addTeacher.phone.length !== 11) return this.$message.error("")
            if(this.addTeacher.email === '') return this.$message.error("")
            if(this.addTeacher.ed !=='' && this.addTeacher.ed !=='' && this.addTeacher.ed !==''){
                return this.$message.error("")
            }
            if(this.addTeacher.sex !== '' && this.addTeacher.sex !== '')return this.$message.error("")
            this.addTeacher.age = Number(this.addTeacher.age)
            if(this.addTeacher.age < 1 || this.addTeacher.age > 120)return this.$message.error("")
            this.addTeacher.id = this.total
            const {data:res} = await axios.post('stu-teacher-info/add', this.addTeacher)
            //
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.$message.success("")
            //
            this.addTeacher.name = this.addTeacher.idNum =  this.addTeacher.phone = this.addTeacher.email = ''
            this.addTeacher.age = this.addTeacher.ed = this.addTeacher.sex = ''
            await this.getTeacherInfoList()
            this.addTeacherDialogVisible = false
        },
    }
}
</script>

<style scoped lang="less">

</style>