<template>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card style="width: 100%;height: auto">
        <el-button type="primary" @click="addCourseDialogVisible = true"></el-button>
        <el-table :data="courseList" border stripe>
            <el-table-column  label="" type="expand" prop="tagList">
                <template #default="scope"  >
                    <el-tag v-if="scope.row.tagList[0] !== null" closable @close="removeTeacherCourseById(scope.row, item.id)"
                            v-for="item in scope.row.tagList" :disable-transitions="false" style="margin-right: 15px">{{item.name}}</el-tag>
                    <el-button class="button-new-tag" size="small" @click="addTeacherToCourse(scope.row)">+ New Tag</el-button>
                </template>
            </el-table-column>
            <el-table-column label="id" align="center" prop="id"></el-table-column>
            <el-table-column label="" align="center" prop="name"></el-table-column>
            <el-table-column label="" align="center"  prop="scoreValue"></el-table-column>
            <el-table-column label="" align="center" prop="period"></el-table-column>
            <el-table-column label="" align="center" prop="finished"></el-table-column>
            <el-table-column label=""  align="center" >
                <template #default="scope">
                    <el-button size="mini" type="primary" @click="editCourseShowDialogShow(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" type="danger" @click="delCourseShowDialogShow(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
    <el-dialog title="" v-model="addCourseDialogVisible" width="30%">
        <el-form :model="addCourse">
            <el-form-item label="" label-width="120px">
                <el-input v-model="addCourse.name" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addCourse.scoreValue" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="addCourse.period" ></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
                <span class="dialog-footer">
                  <el-button @click="addCourseDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="addCourseInfoHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
    <el-dialog title="" v-model="editCourseDialogVisible" width="30%">
        <el-form :model="editCourse">
            <el-form-item label="" label-width="120px">
                <el-input v-model="editCourse.name" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="editCourse.scoreValue" ></el-input>
            </el-form-item>
            <el-form-item label="" label-width="120px">
                <el-input v-model="editCourse.period" ></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
                <span class="dialog-footer">
                  <el-button @click="editCourseDialogVisible = false"> </el-button>
                  <el-button type="primary" @click="editCourseInfoHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
    <el-dialog title="" v-model="delCourseDialogVisible" width="30%" >
        <span></span>
        <template #footer>
                <span class="dialog-footer">
                    <el-button @click="delCourseDialogVisible = false;delCourse={}"> </el-button>
                    <el-button type="danger" @click="delCourseInfoHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
    <el-dialog title="" v-model="addTeacherToCourseDialogVisible" width="30%" >
        <el-select v-model="teacherValue.teacherId" placeholder="">
            <el-option v-for="item in teacherOptions" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <template #footer>
                <span class="dialog-footer">
                    <el-button @click="addTeacherToCourseDialogVisible = false;"> </el-button>
                    <el-button type="danger" @click="addTeacherToCourseHandel"> </el-button>
                </span>
        </template>
    </el-dialog>
</template>

<script>
import axios from "axios";

export default{
    //
    name:'CourseInfo',
    data(){
        return {
            //
            queryInfo:{
                pageNum:1,
                pageSize:20
            },
            courseList:[],
            total:0,
            addCourseDialogVisible:false,
            addCourse:{
                name:'',
                scoreValue:'',
                period:''
            },
            editCourseDialogVisible:false,
            editCourse:{
                name:'',
                scoreValue:'',
                period:''
            },
            delCourseDialogVisible:false,
            delCourse:{
                name:'',
                scoreValue:'',
                period:''
            },
            addTeacherToCourseDialogVisible:false,
            teacherOptions: [],
            teacherValue: {
                teacherId:'',
                courseId:''
            }
        }
    },
    created() {
        this.getCourseList()
    },
    methods:{
        //
        async getCourseList(){
            const {data:res} = await axios.get("/stu-course/get",{params:this.queryInfo})
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.courseList = res.data.infoList
            this.total = res.data.numOfTotalRecord
        },
        async addCourseInfoHandel(){
            if(this.addCourse.name === '')return this.$message.error("")
            if(this.addCourse.scoreValue === '')return this.$message.error("")
            if(this.addCourse.period === '')return this.$message.error("")
            if(isNaN(Number(this.addCourse.scoreValue))) return this.$message.error("")
            if(isNaN(Number(this.addCourse.period))) return this.$message.error("")
            this.addCourse.id = this.total
            const {data:res} = await axios.post("stu-course/add",this.addCourse)
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.addCourseDialogVisible = false
            this.$message.success("")
            await this.getCourseList()
        },
        async removeTeacherCourseById(row,id){
            const {data:res} = await axios.get("stu-teacher-info/remove-course-teacher", {params:{"teacherId":id}})
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.$message.success("")
            await this.getCourseList()
        },
        editCourseShowDialogShow(id,row) {
            this.editCourse = row
            this.editCourseDialogVisible = true
        },
        async editCourseInfoHandel(){
            const {data:res} = await axios.post("stu-course/edit",this.editCourse)
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.editCourseDialogVisible = false
            this.$message.success("")
        },
        async delCourseInfoHandel(){
            const {data:res} = await axios.delete("stu-course/del", {data:this.delCourse})
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.delCourseDialogVisible = false
            this.$message.success("")
            await this.getCourseList()
        },
        async delCourseShowDialogShow(id,row){
            this.delCourse = row
            this.delCourseDialogVisible = true
        },
        async addTeacherToCourse(row){
            const {data:res} = await axios.get("stu-teacher-info/get-no-course")
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.teacherOptions = res.data.infoList
            this.teacherValue.courseId = row.id
            this.addTeacherToCourseDialogVisible = true
        },
        async addTeacherToCourseHandel(){
            if(this.teacherValue.teacherId === '')return this.$message.error('')
            const {data:res} = await axios.get("stu-teacher-info/set-course",{params:this.teacherValue})
            if(res.meta.status !== 200){
                return this.$message.error(res.meta.msg)
            }
            this.teacherValue={}
            this.addTeacherToCourseDialogVisible = false
            this.$message.success("")
            await this.getCourseList()
        }
    }
}
</script>

<style scoped lang="less">
//css
</style>
